<template>
  <div class="flex xl:flex-row flex-col justify-between mb-8">
    <Breadcrumb pageTitle="Edit Meta" :backTo="{ name: 'settingSeo' }" :items="breadcrumb" />
  </div>
  <Card class="mb-6">
    <SeoForm v-model:data="data" ref="refSeoForm" />
    <template #footer>
      <div class="flex justify-end">
        <Button @click="updateMeta" :loading="loading.update" view="smooth" color="teal">Save</Button>
      </div>
    </template>
  </Card>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useShowSeo } from 'composable/setting'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import { useRoute } from 'vue-router'
import { ElMessage } from 'element-plus'
import SeoForm from 'components/Seo/Form.vue'

export default defineComponent({
  components: {
    Card,
    Input,
    Button,
    Breadcrumb,
    SeoForm
  },
  async setup(props, { emit }) {
    const route = useRoute()
    const { state, data, loading, actionShow, actionUpdate } = useShowSeo()
    const refSeoForm = ref(null)
    const breadcrumb = [{
      text: 'Settings',
      to: {
        name: 'setting'
      }
    }, {
      text: 'SEO',
      to: {
        name: 'settingSeo'
      }
    },{
      text: 'Edit Meta'
    }]

    await Promise.all([
      actionShow(route.params.id)
    ])

    const updateMeta = async () => {
      const isValidated = refSeoForm.value.validate()
      if(isValidated) await actionUpdate(route.params.id, () => {
        ElMessage({
          type: 'success',
          message: 'SEO meta updated!'
        })
      })
    }

    return {
      refSeoForm,
      state,
      data,
      loading,
      breadcrumb,
      updateMeta
    }
  }
})
</script>